import Vue from 'vue'
import VueRouter from 'vue-router'
import TreeLogin from '@/components/user/TreeLogin.vue'
import TreeRegister from '@/components/user/TreeRegister.vue'
import TreeHomepage from '@/components/user/TreeHomepage.vue'
import TreeEmpty from '@/components/error/TreeEmpty.vue'
import TreeGraph from '@/components/graph/graph.vue'

Vue.use(VueRouter)

const Routers = [
  // 
  {
    path: '*',
    redirect: '/empty'
  },
  {
    path: '/',
    redirect: '/login'
  },
  // 
  {
    path: '/login',
    meta: {
      title: '-(FTS)'
    },
    name: 'TreeLogin',
    component: TreeLogin
  },
  {
    path: '/register',
    meta: {
      title: '-(FTS)'
    },
    name: 'TreeRegister',
    component: TreeRegister
  },
  {
    path: '/homepage/:nickname',
    meta: {
      title: '-(FTS)'
    },
    name: 'TreeHomepage',
    component: TreeHomepage
  },
  {
    path: '/empty',
    meta: {
      title: '(FTS)'
    },
    name: 'TreeEmpty',
    component: TreeEmpty
  },
  {
    path: '/tree/:treeName',
    meta: {
      title: '-(FTS)'
    },
    name: 'TreeGraph',
    component: TreeGraph
  }
]

const RouterConfig = {
  mode: 'hash',
  routes: Routers
}

const router = new VueRouter(RouterConfig)
router.beforeEach((to, from, next) => {
  /* to: 
  from: 
  next:  */
  window.document.title = to.meta.title
  next()
})
router.afterEach((to, from, next) => {
  window.scrollTo(0, 0)
})

export default router



