<template>
<div>
  <template v-if="utilIndex === '3-1'">
    <el-form :inline="true" :model="this.addingNode" class="cssTreeUtilShowDetail">
      <p></p>
      <el-form-item>
        <el-input v-model="addingNode.name" placeholder="" max="5"></el-input>
      </el-form-item>
      <el-form-item>
        <el-select v-model="addingNode.gender" placeholder="">
          <el-option
            v-for="type in genderType"
            :key="type"
            :label="type"
            :value="type">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-input v-model="addingNode.birthTime" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-input v-model="addingNode.deathTime" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-input v-model="addingNode.majorAchievements" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click.native="addNode()" type="primary" plain></el-button>
      </el-form-item>
    </el-form>
  </template>
  <template v-if="utilIndex === '3-2'">
    <el-form :inline="true" :model="this.addingRelationship" class="cssTreeUtilShowDetail">
      <p></p>
      <el-form-item>
        <el-input v-model="addingRelationship.source" placeholder="" max="5"></el-input>
      </el-form-item>
      <el-form-item>
        <el-select v-model="addingRelationship.relationshipName" placeholder="">
          <el-option
            v-for="type in relationshipType"
            :key="type"
            :label="type"
            :value="type">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-input v-model="addingRelationship.target" placeholder="" max="5"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click.native="addRelationship()" type="primary" plain></el-button>
      </el-form-item>
    </el-form>
  </template>
  <template v-if="utilIndex === '4-1'">
    <el-form :inline="true" :model="this.deletingNode" class="cssTreeUtilShowDetail">
      <p></p>
      <el-form-item>
        <el-input v-model="deletingNode.name" placeholder="" max="5"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click.native="deleteNode()" type="primary" plain></el-button>
      </el-form-item>
    </el-form>
  </template>
  <template v-if="utilIndex === '4-2'">
    <el-form :inline="true" :model="this.deletingRelationship" class="cssTreeUtilShowDetail">
      <p></p>
      <el-form-item>
        <el-input v-model="deletingRelationship.source" placeholder="" max="5"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input v-model="deletingRelationship.target" placeholder="" max="5"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click.native="deleteRelationship()" type="primary" plain></el-button>
      </el-form-item>
    </el-form>
  </template>
  <template v-if="utilIndex === '6'">
    <template v-if="editState === false">
      <el-form :inline="true" class="cssTreeUtilShowDetail">
        <p></p>
        <el-form-item>
          <el-autocomplete
            class="inline-input"
            v-model="queryNodeName"
            :fetch-suggestions="querySearchAsyncNodeInfor"
            placeholder=""></el-autocomplete>
        </el-form-item>
        <el-form-item>
          <el-button @click.native="getNodeInfo()" type="primary" plain></el-button>
          <el-button @click.native="initializeNodeInfo()" type="primary" plain></el-button>
        </el-form-item>
      </el-form>
      <el-divider></el-divider>
      <div class="cssTreeUtilNodeInfoDetail" style="width: 220px;">
        <el-scrollbar style="height: 100%; width: 100%;">
          <p> {{ this.queryNodeInfo.name }}</p>
          <p> {{ this.queryNodeInfo.gender }}</p>
          <p> {{ this.queryNodeInfo.birthTime }}</p>
          <p> {{ this.queryNodeInfo.deathTime }}</p>
          <p>{{ this.queryNodeInfo.majorAchievements }}</p>
          <el-button @click.native="showEditNodeInfoPanel()" type="primary" plain></el-button>
        </el-scrollbar>
      </div>
    </template>
    <template v-else>
      <el-form ref="queryNodeInfo" :model="this.queryNodeInfo" label-position="left"
          class="cssTreeUtilShowDetail" style="height: 500px; width: 200px">
        <el-scrollbar style="height: 100%; width: 100%;">
          <p>{{ queryNodeInfo.name }}</p>
          <el-form-item label=":" placeholder=":">
            <el-select v-model="queryNodeInfo.gender" placeholder="">
              <el-option
                v-for="type in genderType"
                :key="type"
                :label="type"
                :value="type">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" placeholder=":">
            <el-input v-model="queryNodeInfo.birthTime"></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-input v-model="queryNodeInfo.deathTime" placeholder=":"></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-input
              type="textarea"
              :autosize="{ minRows: 4, maxRows: 6 }"
              placeholder=""
              v-model="queryNodeInfo.majorAchievements"></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-input
              type="textarea"
              :autosize="{ minRows: 3, maxRows: 4 }"
              maxlength="50"
              show-word-limit
              placeholder=""
              v-model="queryNodeInfo.commit"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click.native="showEditNodeInfoPanel()" type="primary" plain></el-button>
            <el-button @click.native="editNodeInfo()" type="primary" plain></el-button>
          </el-form-item>
        </el-scrollbar>
      </el-form>
    </template>
  </template>
  <template v-if="utilIndex === '7-1'">
    <el-form :inline="true" :model="this.queryShortestpathVO" class="cssTreeUtilShowDetail">
      <p></p>
      <el-form-item>
        <el-input v-model="queryShortestpathVO.startPersonName" placeholder="" max="5"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input v-model="queryShortestpathVO.endPersonName" placeholder="" max="5"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click.native="findShortestpath()" type="primary" plain></el-button>
      </el-form-item>
    </el-form>
  </template>
  <template v-if="utilIndex === '8'">
    <div style="height: 500px;width: 220px;">
      <el-scrollbar style="height: 100%; width: 100%;">
        <h2></h2>
        <p>
          <el-button @click.native="dialogFormVisible.transferAdmin = true" type="text" plain></el-button>
          <el-dialog title="" :visible.sync="dialogFormVisible.transferAdmin" width="25%">
            <el-form size="medium" style="margin: 0px 10px;">
              <p><span>{{this.treeDetailInfo.admin}}</span></p>
              <el-form-item>
                <el-select v-model="newAdminNickname" placeholder="">
                  <el-option
                    v-for="follower in followersNicknameList"
                    :key="follower"
                    :label="follower"
                    :value="follower">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="transferAdmin()"></el-button>
                <el-button @click="dialogFormVisible.transferAdmin = false; getTreeDetailInfo()"></el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
        </p>
        <p>
          <el-button @click.native="dialogFormVisible.changeCenterNode = true" type="text" plain></el-button>
          <el-dialog title="" :visible.sync="dialogFormVisible.changeCenterNode" width="25%">
            <el-form size="medium" style="margin: 0px 10px;">
              <p><span>{{this.treeDetailInfo.defaultCenterPerson}}</span></p>
              <el-form-item>
                <el-select v-model="newCenterNodeName" placeholder="">
                  <el-option
                    v-for="node in nodesNameList"
                    :key="node"
                    :label="node"
                    :value="node">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="changeCenterNode()"></el-button>
                <el-button @click="dialogFormVisible.changeCenterNode = false; getTreeDetailInfo()"></el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
        </p>
        <p>
          <el-button @click.native="dialogFormVisible.changeDescription = true" type="text" plain></el-button>
          <el-dialog title="" :visible.sync="dialogFormVisible.changeDescription" width="25%">
            <el-form size="medium" style="margin: 0px 10px;">
              <el-form-item label=":">
                <el-input v-model="treeDetailInfo.name" placeholder="" max="20"></el-input>
              </el-form-item>
              <el-form-item label=":">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 4, maxRows: 6 }"
                  placeholder=""
                  v-model="treeDetailInfo.description"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="changeDescription"></el-button>
                <el-button @click="dialogFormVisible.changeDescription = false; getTreeDetailInfo()"></el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
        </p>
        <p>
          <el-button @click.native="dialogFormVisible.cancelFollow = true; this.getFollowers();" type="text" plain></el-button>
          <el-dialog title="" :visible.sync="dialogFormVisible.cancelFollow" width="25%">
            <el-form size="medium" style="margin: 0px 10px;">
              <el-form-item>
                <el-select v-model="cancelFollowUserName" placeholder="">
                  <el-option
                    v-for="follower in followersNicknameList"
                    :key="follower"
                    :label="follower"
                    :value="follower">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="cancelFollow()"></el-button>
                <el-button @click="dialogFormVisible.cancelFollow = false"></el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
        </p>
      </el-scrollbar>
      </div>
  </template>
  <template v-if="utilIndex === null">
    <div class="cssTreeUtilShowDetail" style="width: 220px;">
      <el-scrollbar style="height: 100%; width: 100%;">
        <h1></h1>
        <p>ID<span>{{this.treeDetailInfo.id}}</span></p>
        <div>
          <p>{{this.treeDetailInfo.admin}}</p>
        </div>
        <p><span>{{this.treeDetailInfo.defaultCenterPerson}}</span></p>
        <div>
          <p>{{this.treeDetailInfo.description}}</p>
        </div>
      </el-scrollbar>
      </div>
  </template>
</div>
</template>
<script>
export default {
  name: 'graphUtil',
  props: ['myChart', 'utilIndex'],
  data () {
    return {
      addingNode: {
        name: '',
        gender: '',
        birthTime: '',
        deathTime: '',
        majorAchievements: ''
      },
      deletingNode: {
        name: ''
      },
      addingRelationship: {
        source: '',
        target: '',
        relationshipName: ''
      },
      deletingRelationship: {
        source: '',
        target: ''
      },
      queryNodeName: '',
      editState: false,
      relationshipType: ['', '', '', '', ''],
      genderType: ['', ''],
      // 
      nodesNameList: [],
      followersNicknameList: [],
      // 
      treeDetailInfo: {
        id: '',
        name: '',
        admin: '',
        defaultCenterPerson: '',
        description: ''
      },
      timeout: null,
      queryNodeInfo: {
        name: '',
        gender: '',
        birthTime: '',
        deathTime: '',
        majorAchievements: '',
        commit: ''
      },
      queryShortestpathVO: {
        startPersonName: '',
        endPersonName: ''
      },
      dialogFormVisible: {
        transferAdmin: false,
        changeCenterNode: false,
        changeDescription: false,
        cancelFollow: false
      },
      newCenterNodeName: '',
      newAdminNickname: '',
      newTreeName: '',
      cancelFollowUserName: '',
      circleGraphData: {
        nodes: [],
        links: []
      }
    }
  },
  mounted () {
    this.getFollowers()
    this.getNodesNameList()
    this.getTreeDetailInfo()
    var _this = this
    // 
    this.bus.$on('on-clickNode', function (clickedNodeName) {
      // 
      _this.queryNodeName = clickedNodeName
      _this.$axios
        .get('/tree/' + _this.$route.params.treeName + '/node', {
          params: {
            name: clickedNodeName
          }
        })
        .then(response => {
          if (response.data.code === 200) {
            _this.queryNodeInfo = response.data.data
          }
        })
        .catch(error => {
          console.log(error)
        })
    })
  },
  methods: {
    // 
    getTreeCircleNodeAndLinkData (centerNodeName) {
      this.myChart.showLoading()
      this.$axios
        .get('/tree/' + this.$route.params.treeName + '/circle-node-relationship-data', {
          params: {
            centerNode: centerNodeName,
            radius: 1000
          }
        })
        .then(response => {
          if (response.data.code === 200) {
            this.circleGraphData.nodes = response.data.data[0]
            this.circleGraphData.links = this.loadLink(response)
            this.myChart.setOption({
              series: [{
                data: this.circleGraphData.nodes,
                links: this.circleGraphData.nodes
              }]
            })
          } else {
            this.$alert(response.data.message)
          }
          this.myChart.hideLoading()
        })
        .catch(error => {
          console.log(error)
        })
    },
    addNode () {
      if (this.addingNode.name === '') {
        this.$alert('')
        return null
      }
      if (this.addingNode.gender === '') {
        this.$alert('')
        return null
      }
      this.$axios
        .post('/tree/' + this.$route.params.treeName + '/node', {
          name: this.addingNode.name,
          gender: this.addingNode.gender,
          birthTime: this.addingNode.birthTime,
          deathTime: this.addingNode.deathTime,
          majorAchievements: this.addingNode.majorAchievements
        })
        .then(response => {
          if (response.data.code === 200) {
            this.getTreeCircleNodeAndLinkData(this.addingNode.name)
            this.$alert(response.data.message)
            // 
            this.addingNode.name = ''
            this.addingNode.gender = ''
            this.addingNode.birthTime = ''
            this.addingNode.deathTime = ''
            this.addingNode.majorAchievements = ''
            this.addingNode.commit = ''
          }
          this.$alert(response.data.message)
        })
        .catch(response => {})
    },
    deleteNode () {
      if (this.deletingNode.name === '') {
        this.$alert('')
        return null
      }
      this.$axios
        .delete('/tree/' + this.$route.params.treeName + '/node/' + this.deletingNode.name)
        .then(response => {
          if (response.data.code === 200) {
            this.$emit('handleInitializeHashNodeAndLinkData')
            this.$emit('handleGetTreeHashNodeAndLinkData')
            this.deletingNode.name = ''
          }
          this.$alert(response.data.message)
        })
        .catch(response => {})
    },
    addRelationship () {
      if (this.addingRelationship.source === '' || this.addingRelationship.target === '') {
        this.$alert('')
        return null
      }
      if (this.addingRelationship.relationshipName === '') {
        this.$alert('')
        return null
      }
      this.$axios
        .post('/tree/' + this.$route.params.treeName + '/relationship', {
          source: this.addingRelationship.source,
          target: this.addingRelationship.target,
          relationshipName: this.addingRelationship.relationshipName
        })
        .then(response => {
          if (response.data.code === 200) {
            this.getTreeCircleNodeAndLinkData(this.addingRelationship.source)
            this.addingRelationship.source = ''
            this.addingRelationship.target = ''
            this.addingRelationship.relationshipName = ''
          }
          this.$alert(response.data.message)
        })
        .catch(error => {
          console.log(error)
        })
    },
    deleteRelationship () {
      if (this.deletingRelationship.source === '' || this.deletingRelationship.target === '') {
        this.$alert('')
        return null
      }
      this.$axios
        .delete('/tree/' + this.$route.params.treeName + '/relationship', {
          params: {
            source: this.deletingRelationship.source,
            target: this.deletingRelationship.target
          }
        })
        .then(response => {
          if (response.data.code === 200) {
            this.$emit('handleInitializeHashNodeAndLinkData')
            this.$emit('handleGetTreeHashNodeAndLinkData')
            this.deletingRelationship.source = ''
            this.deletingRelationship.target = ''
          }
          this.$alert(response.data.message)
        })
        .catch(response => {})
    },
    getNodeInfo () {
      if (this.queryNodeName === '') {
        this.$alert('')
        return null
      }
      this.$axios
        .get('/tree/' + this.$route.params.treeName + '/node', {
          params: {
            name: this.queryNodeName
          }
        })
        .then(response => {
          if (response.data.code === 200) {
            this.queryNodeInfo = response.data.data
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    // 
    initializeNodeInfo () {
      this.queryNodeName = null
      this.queryNodeInfo.name = null
      this.queryNodeInfo.birthTime = null
      this.queryNodeInfo.deathTime = null
      this.queryNodeInfo.majorAchievements = null
    },
    // 
    showEditNodeInfoPanel () {
      if (this.queryNodeInfo.name !== null) {
        this.editState = !this.editState
      }
    },
    // 
    editNodeInfo () {
      this.$axios
        .put('/tree/' + this.$route.params.treeName + '/node-info', {
          name: this.queryNodeInfo.name,
          birthTime: this.queryNodeInfo.birthTime,
          deathTime: this.queryNodeInfo.deathTime,
          majorAchievements: this.queryNodeInfo.majorAchievements,
          commit: this.queryNodeInfo.commit
        })
        .then(response => {
          if (response.data.code === 200 || response.data.code === 400) {
            this.editState = false
            this.queryNodeInfo.commit = ''
            this.$alert(response.data.message)
          }
        })
        .catch(response => {})
    },
    // 
    getNodesNameList () {
      this.$axios
        .get('/tree/' + this.$route.params.treeName + '/nodes')
        .then(response => {
          if (response.data.code === 200) {
            this.nodesNameList = response.data.data
          }
        })
        .catch(response => {})
    },
    // 
    getFollowers () {
      this.$axios
        .get('/tree/' + this.$route.params.treeName + '/followers')
        .then(response => {
          if (response.data.code === 200) {
            this.followersNicknameList = response.data.data
          }
        }
        )
        .catch(response => {})
    },
    getTreeDetailInfo () {
      this.$axios
        .get('/tree/' + this.$route.params.treeName)
        .then(response => {
          if (response.data.code === 200) {
            this.treeDetailInfo.id = response.data.data.genealogyId
            this.treeDetailInfo.name = response.data.data.genealogyName
            this.treeDetailInfo.admin = response.data.data.genealogyAdmin
            this.treeDetailInfo.defaultCenterPerson = response.data.data.genealogyDefaultCenterPerson
            this.treeDetailInfo.description = response.data.data.genealogyDescription
          }
        })
        .catch(response => {})
    },
    querySearchAsyncNodeInfor (queryString, cb) {
      var nodesNameListLength = this.nodesNameList.length
      var queryNodesList = []
      for (var i = 0; i < nodesNameListLength; i++) {
        queryNodesList.push(
          {
            'value': this.nodesNameList[i]
          }
        )
      }
      var results = queryString ? queryNodesList.filter(this.createFilter(queryString)) : queryNodesList
      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        cb(results)
      }, 3000 * Math.random())
    },
    // 
    createFilter (queryString) {
      return (queryObject) => {
        return (queryObject.value.indexOf(queryString) === 0)
      }
    },
    findShortestpath () {
      this.myChart.showLoading()
      // 
      this.$axios
        .get('/tree/' + this.$route.params.treeName + '/node/shortestpath', {
          params: {
            startPersonName: this.queryShortestpathVO.startPersonName,
            endPersonName: this.queryShortestpathVO.endPersonName,
            radius: 10000
          }})
        .then(response => {
          if (response.data.code === 200) {
            var nodes = []
            var links = []
            nodes = this.loadNode(response)
            links = this.loadLink(response)
            this.myChart.setOption({
              series: [{
                data: nodes,
                links: links
              }]
            })
            this.myChart.hideLoading()
          }
          if (response.data.code !== 200) {
            this.$alert(response.data.message)
          }
        })
        .catch(response => {})
    },
    loadNode (response) {
      var nodes = []
      // 
      var nodesList = response.data.data[0]
      var nodesNum = nodesList.length
      // null
      if (nodesNum === 0) {
        return null
      }
      for (var i = 0; i < nodesNum; i++) {
        var node = {
          name: nodesList[i].name,
          x: Number(nodesList[i].x),
          y: Number(nodesList[i].y)
        }
        nodes.push(node)
      }
      return nodes
    },
    loadLink (response) {
      var links = []
      // 
      var linksList = response.data.data[1]
      var linksNum = linksList.length
      for (var j = 0; j < linksNum; j++) {
        links.push({
          source: linksList[j].source,
          target: linksList[j].target,
          label: {
            formatter: linksList[j].relationshipName
          }
        })
      }
      return links
    },
    transferAdmin () {
      this.$axios
        .patch('/tree/' + this.$route.params.treeName + '/admin/' + this.newAdminNickname)
        .then(response => {
          if (response.data.code === 200) {
            this.dialogFormVisible.transferAdmin = false
          }
          this.$alert(response.data.message)
        })
        .catch(response => {})
    },
    changeCenterNode () {
      this.$axios
        .patch('/tree/' + this.$route.params.treeName + '/center-node', {
          name: this.newCenterNodeName
        })
        .then(response => {
          if (response.data.code === 200) {
            this.dialogFormVisible.changeCenterNode = false
            this.getTreeDetailInfo()
            this.$emit('handleGetTreeMainNodeData')
          }
          this.$alert(response.data.message)
        })
        .catch(response => {})
    },
    changeDescription () {
      this.$axios
        .patch('/tree/' + this.$route.params.treeName + '/name-and-description', {
          newGenealogyName: this.treeDetailInfo.name,
          newDescription: this.treeDetailInfo.description
        })
        .then(response => {
          if (response.data.code === 200) {
            this.dialogFormVisible.changeDescription = false
          }
          this.getTreeDetailInfo()
          this.$alert(response.data.message)
        })
        .catch(response => {})
    },
    cancelFollow () {
      this.$axios
        .delete('/tree/' + this.$route.params.treeName + '/user/' + this.cancelFollowUserName)
        .then(response => {
          if (response.data.code === 200) {
            this.dialogFormVisible.cancelFollow = false
            this.cancelFollowUserName = ''
          }
          this.$alert(response.data.message)
        })
        .catch(response => {})
    }
  }
}
</script>
<style scoped>
.cssTreeUtilShowDetail {
  display: flex;
  flex-flow: column nowrap;
  /* */
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
}
.cssTreeUtilNodeInfoDetail {
  display: flex;
  flex-flow: column nowrap;
  /* */
  justify-content: center;
  align-items: flex-start;
  height: 230px;
}
</style>
<style>
.el-scrollbar__wrap{
  overflow-x: hidden;
