<template>
<div class="cssTree">
  <div class="cssTreePanelContainer">
    <div class="cssTreeUtilNavDetail">
      <el-menu :collapse='true' @select="handleSelect" style="border-right: 0px;">
        <el-menu-item index="1">
          <i class="el-icon-refresh" @click="getTreeHashNodeAndLinkData()"></i>
        </el-menu-item>
        <el-menu-item index="2">
          <i class="el-icon-view" @click="changeLabel()"></i>
        </el-menu-item>
        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-plus"></i>
          </template>
          <el-menu-item-group>
            <el-menu-item index="3-1"></el-menu-item>
          </el-menu-item-group>
          <el-menu-item-group>
            <el-menu-item index="3-2"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-submenu index="4">
          <template slot="title">
            <i class="el-icon-minus"></i>
          </template>
          <el-menu-item-group>
            <el-menu-item index="4-1"></el-menu-item>
          </el-menu-item-group>
          <el-menu-item-group>
            <el-menu-item index="4-2"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-menu-item index="5">
          <i class="el-icon-chat-dot-round"></i>
        </el-menu-item>
        <el-menu-item index="6">
          <i class="el-icon-search"></i>
        </el-menu-item>
        <el-submenu index="7">
          <template slot="title">
            <i class="el-icon-menu"></i>
          </template>
          <el-menu-item-group>
            <span slot="title"></span>
            <el-menu-item index="7-1"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-menu-item index="8">
          <i class="el-icon-s-management"></i>
        </el-menu-item>
        <el-menu-item index="9">
          <i class="el-icon-setting"></i>
        </el-menu-item>
      </el-menu>

    </div>
    <div class="cssTreeShowDetail">
      <el-card id="treeShow" class="cssTreeShowPanel"></el-card>
    </div>
    <div class="cssTreeUtilDetail">
      <el-card class="cssTreeUtilShowPanel">
        <graph-util
          :myChart="this.myChart"
          :utilIndex="this.selectUtilIndex"
          @handleGetTreeHashNodeAndLinkData="getTreeHashNodeAndLinkData"
          @handleInitializeHashNodeAndLinkData="initializeHashNodeAndLinkData"></graph-util>
      </el-card>
    </div>
  </div>
</div>
</template>
<script>
import graphUtil from '@/components/graph/graphUtil/graphUtil.vue'

export default{
  name: 'TreeGraph',
  components: {
    graphUtil
  },
  data () {
    return {
      // index
      selectUtilIndex: null,
      // 
      myChart: null,
      // 
      label: {
        nodeLabel: {
          show: true
        },
        edgeLabel: {
          show: true
        }
      },
      // 
      radius: 1000,
      maxIterationNum: 2,
      groupNumber: 1,
      // 
      hashGraphData: {
        nodes: [],
        links: []
      },
      // 
      queryShortestpathVO: {
        startPersonName: '',
        endPersonName: ''
      },
      utilNavClickState: {
        load: false
      }
    }
  },
  mounted () {
    this.drawGraph()
    this.getTreeInfoData()
    this.getTreeHashNodeAndLinkData()
  },
  methods: {
    drawGraph () {
      this.myChart = this.$echarts.init(document.getElementById('treeShow'))
      // 
      var option = {
        title: {
          text: this.$route.params.treeName
        },
        // 
        tooltip: {
          show: true,
          trigger: 'item',
          formatter: '{b}'
        },
        // 
        animationDurationUpdate: 1500,
        // 
        animationEasingUpdate: 'quinticInOut',
        series: [{
          // 
          type: 'graph',
          // 
          layout: 'none',
          // 
          symbolSize: 25,
          // 
          // 'scale''move'true
          roam: true,
          // 0
          nodeScaleRatio: 0.1,
          // 
          focusNodeAdjacency: true,
          itemStyle: {
            color: '#1C86EE',
            opacity: 0.9
          },
          lineStyle: {
            curveness: 0.3
          },
          label: {
            show: this.label.nodeLabel.show,
            fontSize: 15,
            // 
            align: 'center'
          },
          /* 
             */
          edgeSymbol: ['circle', 'arrow'],
          edgeSymbolSize: [4, 10],
          edgeLabel: {
            normal: {
              show: this.label.edgeLabel.show,
              formatter: '',
              align: 'center',
              textStyle: {
                fontSize: 15
              }
            }
          },
          emphasis: {
            label: {
              show: true,
              align: 'center'
            },
            edgeLabel: {
              show: true,
              fontWeight: 'bold',
              align: 'center'
            }
          },
          data: this.hashGraphData.nodes,
          links: this.hashGraphData.links
        }]
      }
      this.myChart.setOption(option)
      var _this = this
      this.myChart.on('click', function (params) {
        if (params.componentType === 'series') {
          if (params.seriesType === 'graph') {
            if (params.dataType === 'node') {
              _this.selectUtilIndex = '6'
              _this.bus.$emit('on-clickNode', params.name)
            }
          }
        }
      })
    },
    getTreeHashNodeAndLinkData () {
      if (this.utilNavClickState.load === false) {
        this.myChart.showLoading()
        this.utilNavClickState.load = true
        this.$axios
          .get('/tree/' + this.$route.params.treeName + '/hash-node-relationship-data', {
            params: {
              groupNum: this.groupNumber
            }
          })
          .then(response => {
            if (response.data.code === 200) {
              this.hashGraphData.nodes = this.hashGraphData.nodes.concat(response.data.data[0])
              this.hashGraphData.links = this.hashGraphData.links.concat(this.loadLink(response))
              this.groupNumber += 1
            } else {
              this.$alert(response.data.message)
            }
            this.myChart.setOption({
              series: [{
                data: this.hashGraphData.nodes,
                links: this.hashGraphData.links
              }]
            })
            this.myChart.hideLoading()
            this.utilNavClickState.load = false
          })
          .catch(response => {})
      }
    },
    initializeHashNodeAndLinkData () {
      this.groupNumber = 1
      this.hashGraphData.nodes = []
      this.hashGraphData.links = []
    },
    getTreeInfoData () {},
    expandTree () {
    },
    loadNode (response) {
      var nodes = []
      // 
      var nodesList = response.data.data[0]
      var nodesNum = nodesList.length
      // null
      if (nodesNum === 0) {
        return null
      }
      for (var i = 0; i < nodesNum; i++) {
        var node = {
          name: nodesList[i].name,
          x: Number(nodesList[i].x),
          y: Number(nodesList[i].y)
        }
        nodes.push(node)
      }
      return nodes
    },
    loadLink (response) {
      var links = []
      // 
      var linksList = response.data.data[1]
      var linksNum = linksList.length
      for (var j = 0; j < linksNum; j++) {
        links.push({
          source: linksList[j].source,
          target: linksList[j].target,
          label: {
            formatter: linksList[j].relationshipName
          }
        })
      }
      return links
    },
    handleSelect (index) {
      if (index === '1' || index === '2') {
      } else {
        this.selectUtilIndex = index
      }
    },
    changeLabel () {
      this.label.nodeLabel.show = !this.label.nodeLabel.show
      this.label.edgeLabel.show = !this.label.edgeLabel.show
      this.myChart.setOption({
        series: [{
          label: {
            show: this.label.nodeLabel.show
          },
          edgeLabel: {
            normal: {
              show: this.label.edgeLabel.show
            }
          }
        }]
      })
    }
  }
}
</script>
<style scoped>
.cssTree{
  width: 100%;
  margin: 0;
}
.cssTreePanelContainer {
  display: flex;
  flex-flow: row nowrap;
  /* */
  justify-content: flex-start;
  align-items: flex-start;
  width: 1140px;
  margin: 0 auto;
  margin-top: 60px;
}
.cssTreeUtilNavDetail {
  margin-top: 50px;
  margin-left: 10px;
  height: 100%;
  width: 64px;
}
.cssTreeShowDetail {
  margin: 50px auto 0 auto;
  height: 100%;
}
.cssTreeShowPanel{
  height: 500px;
  width: 800px;
  padding: 0px;
}
.cssTreeUtilDetail {
  margin: 50px 10px 0px 10px;
  width: 246px;
  height: 100%;
}
.cssTreeUtilShowPanel {
  height: 500px;
}
</style>



